<template>
  <div class="text-left">
    <!-- 输入筛选区 -->
    <el-row class="serach-box">
      <el-col :span="4" v-for="item in headerItems" :key="item">
        <el-col :span="6">{{ item.label }}</el-col>
        <el-col :span="17">
          <el-input
            v-model="userQuery[item.prop]"
            clearable
            placeholder="请输入内容"
          >
            <el-button
              @click="query"
              slot="append"
              icon="el-icon-search"
            ></el-button>
          </el-input>
        </el-col>
      </el-col>
      <el-col :span="4">
        <el-date-picker
          v-model="dataValue"
          type="daterange"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          value-format="yyyy-MM-dd"
        >
        </el-date-picker>
      </el-col>
      <el-col class="text-center" :span="4">
        <el-button type="info">导出采购表</el-button>
      </el-col>
    </el-row>
    <!-- 表格区 -->
    <el-row>
      <el-table border :span-method="objectSpanMethod" :data="tableData">
        <el-table-column
          type="index"
          width="50"
          label="序号"
          fixed
        ></el-table-column>
        <el-table-column
          :fixed="item.isFix || false"
          :label="item.label"
          :prop="item.prop"
          :width="item.width"
          v-for="item in tableList"
          :key="item.index"
        >
        </el-table-column>
        <el-table-column width="140" label="操作" fixed="right"
          ><template slot-scope="scope"
            ><el-button size="mini" @click="edit(scope)">文档</el-button
            ><el-button size="mini">图稿</el-button></template
          ></el-table-column
        >
      </el-table>
    </el-row>
  </div>
</template>

<script>
import { GetAllAccessoriesInfo } from "network/sum.js";

export default {
  data() {
    return {
      dataValue: [],
      spanArr: [],
      search: "",
      tableData: [
        { number: 2, sample: "wqwqwqw" },
        { number: 2, sample: "wqwqwqw" },
        { number: 2, sample: "wqwqwqw" },
      ],
      tableList: [
        { label: "订单号", prop: "orderNumber", width: "100", isFix: "left" },
        { label: "业务员", prop: "salesManName", isFix: "left" },
        { label: "客户", prop: "customerName", isFix: "left" },
        { label: "货号", prop: "orderNumber", isFix: "left" },
        { label: "名称及规格", prop: "nameAndStandard", width: "100" },
        { label: "采购方", prop: "sample", width: "100" },
        { label: "供应商", prop: "supplierName", width: "100" },
        { label: "数量", prop: "number", width: "100" },
        { label: "下单日期", prop: "creationTime", width: "100" },
        { label: "单价", prop: "price", width: "100" },
        { label: "备注2", prop: "comment2", width: "100" },
        { label: "总金额", prop: "sample", width: "100" },
        { label: "要求交期", prop: "requestTime", width: "100" },
        { label: "到库情况", prop: "isReady", width: "100" },
        { label: "到库数量", prop: "readyAmount", width: "100" },
        { label: "实际到库日期", prop: "readyTime", width: "100" },
      ],
      headerItems: [
        {
          label: "订单号",
          prop: "orderNumber",
        },
        {
          label: "货号",
          prop: "productId",
        },
        {
          label: "业务员",
          prop: "salesMan",
        },
        {
          label: "客户",
          prop: "customer",
        },
      ],
      userQuery: {
        orderNumber: "",
        productId: "",
        salesMan: "",
        customer: "",
        beginTime: "2021-01-03 03:24:38",
        endTime: "2021-01-17 03:24:38",
        pageSize: 6,
        pageNumber: 1,
      },
    };
  },
  created() {
    this.inital();
  },
  watch: {
    dataValue(val) {
      this.userQuery.beginTime = val[0];
      this.userQuery.endTime = val[1];
      this.query();
    },
  },
  methods: {
    //初始化
    inital() {
      this.getSumList(this.userQuery);
    },
    //获得汇总列表
    getSumList(data) {
      GetAllAccessoriesInfo(data).then((res) => {
        if (res.success) {
          (this.spanArr = []), this.$message.success("获得汇总列表成功");
          this.tableData = res.result.accessories_InfoDto;
          this.getSpanArr();
        }
      });
    },
    //筛选查询
    query() {
      this.getSumList(this.userQuery);
    },

    //数据合并
    getSpanArr() {
      var arr1 = [];
      var arr2 = [];
      var arr3 = [];
      var arr4 = [];
      for (var i = 0; i < this.tableData.length; i++) {
        if (i === 0) {
          arr1.push(1);
          arr2.push(1);
          arr3.push(1);
          arr4.push(1);
          this.pos1 = 0;
          this.pos2 = 0;
          this.pos3 = 0;
          this.pos4 = 0;
        } else {
          if (
            this.tableData[i].orderNumber === this.tableData[i - 1].orderNumber
          ) {
            arr1.push(0);
            arr1[this.pos1] += 1;
            if (
              this.tableData[i].salesManName ===
              this.tableData[i - 1].salesManName
            ) {
              arr2.push(0);
              arr2[this.pos2] += 1;
              if (
                this.tableData[i].customerName ===
                this.tableData[i - 1].customerName
              ) {
                arr3.push(0);
                arr3[this.pos3] += 1;
              } else {
                arr3.push(1);
                this.pos3 = i;
              }
            } else {
              arr2.push(1);
              arr3.push(1);
              this.pos2 = i;
              this.pos3 = i;
            }
          } else {
            arr1.push(1);
            arr2.push(1);
            arr3.push(1);
            this.pos1 = i;
            this.pos2 = i;
            this.pos3 = i;
          }
        }
      }
      this.spanArr.push(arr1);
      this.spanArr.push(arr2);
      this.spanArr.push(arr3);
    },
    //表格数据排序
    objectSpanMethod({ row, column, rowIndex, columnIndex }) {
      switch (columnIndex) {
        case 0: {
          const _row = this.spanArr[0][rowIndex];
          const _col = _row > 0 ? 1 : 0;
          /* console.log(`rowspan:${_row} colspan:${_col}`); */
          return {
            // [0,0] 表示这一行不显示， [2,1]表示行的合并数
            rowspan: _row,
            colspan: _col,
          };
        }
        case 1: {
          const _row = this.spanArr[0][rowIndex];
          const _col = _row > 0 ? 1 : 0;
          /* console.log(`rowspan:${_row} colspan:${_col}`); */
          return {
            // [0,0] 表示这一行不显示， [2,1]表示行的合并数
            rowspan: _row,
            colspan: _col,
          };
        }
        case 2: {
          const _row = this.spanArr[1][rowIndex];
          const _col = _row > 0 ? 1 : 0;
          /* console.log(`rowspan:${_row} colspan:${_col}`); */
          return {
            // [0,0] 表示这一行不显示， [2,1]表示行的合并数
            rowspan: _row,
            colspan: _col,
          };
        }
        case 3: {
          const _row = this.spanArr[2][rowIndex];
          const _col = _row > 0 ? 1 : 0;
          /* console.log(`rowspan:${_row} colspan:${_col}`); */
          return {
            // [0,0] 表示这一行不显示， [2,1]表示行的合并数
            rowspan: _row,
            colspan: _col,
          };
        }
      }
    },
  },
};
</script>

<style scoped>
.el-row {
  margin-bottom: 10px;
}
.serach-box {
  text-align: left;
  line-height: 40px;
}
</style>